.backgroung{
    background-color: #12AAD1;
    height: 500px;
    width: 400px;
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:space-around;
    padding: 38px 0 25px 0;
}

input {
    border: none;
    border-radius: 10px;
    height: 42px;
    width: 80%;
    padding: 6px 0 4px 10px;
    font-family: 'Montserrat', sans-serif;
    font-size: 20px;
}

input::placeholder{
    position: absolute;
    top: 10px;
    left: 10px;
    color: #A3A1A1;
    font-family: 'Montserrat', sans-serif;
    font-size: 20px;
}

button{
    border: none;
    border-radius: 10px;
    background-color: #1F4E5C;
    height: 50px;
    width: 40%;
    color: #ffffff;
    font-size: 20px;
    font-family: 'Montserrat', sans-serif;
}

.quastion{
    height: 113px;
}

p{
    align-self: start;
    padding-left: 40px;
}