.footer__report{
    display: flex;
    justify-content: space-around;
    height: 600px;
    background-color: #1f4e5c;
}

.arrow{
    padding-top: 100px;
    height: 136px;
    width: 314px;
}

.footer__report__info{
    display: flex;
    align-items: start;
    flex-direction: column;
}

.footer__report__info_title{
    text-align: start;
    font-size: 48px;
    font-weight: 600;
}

.footer__report__info_text{
    width: 610px;
    padding: 0;
    text-align: start;
    font-size: 24px;
}

.footer__down{
    background-color: #022C33;
    height: 550px;
}

.footer__down__navbar{
    display: flex;
    justify-content: space-between;
    height: 135px;
    padding: 0px 40px 0 40px;
}
  
.footer__down__navbar__links{
    display: flex;
    gap: 32px;
    padding: 70px 0 0 0;
}
  
.footer__down__novbar__links_link{
    font-size: 32px;
    text-decoration: none;
    text-decoration: underline 1px rgba(0,0,0,0);
    transition: text-decoration-color 300ms;
}
  
.footer__down__novbar__links_link:hover{
    text-decoration: underline;
}
  
.footer__down__logo__name{
    font-size: 64px;
    padding: 40px 0 0 0;
}

.footer__down_photo_wrapper{
    position: absolute;
    left: 0px;
    margin-top: 47px;
}

.footer__down_text{
    padding-top: 320px;
    margin-left: 27%;
}


.footer__down__bottom{
    display: flex;
    justify-content: space-around;
}

li{
    list-style-type:none;
    display: flex;
    padding-top: 30px;
}

li p{
    padding-top: 10px;
}

@media (max-width: 1450px) { 
    .footer__down__navbar__links{
      display: none;
    }
    .arrow{
      display: none;
    }
    .footer__report{
        display: flex;
        flex-direction:column;
    }
  }