.background{
    display: flex;
    background-color: #12AAD1;
    height: 820px;
    width: 700px;
    border-radius: 15px;
    align-items: center;
    flex-direction: column;
    -webkit-transition: all 1s ease;
          transition: all 1s ease; /* Время изменения цвета */
}

.background:hover{
    width: 750px;
    cursor: pointer;
}

.background_photo{
    padding-top: 40px;
}

h1 {
    padding-top: 25px;
    text-align: center;
    font-size: 40px;
}

p {
    text-align: center;
    font-size: 32px;
}