.navbar{
  position: sticky;
  top: 0;
  z-index: 1;
  display: flex;
  justify-content: space-between;
  /* background-color: #1f4e5c; */
  background-color: #022C33;
  height: 135px;
  padding: 0px 40px 0 40px;
}
  
.navbar__links{
  display: flex;
  gap: 42px;
  padding: 70px 0 0 0;
}
  
.novbar__links_link{
  font-size: 32px;
  text-decoration: none;
  text-decoration: underline 1px rgba(0,0,0,0);
  transition: text-decoration-color 300ms;
}

.novbar__links_link:hover{
  text-decoration: underline;
}

.logo__name{
  font-size: 64px;
  padding: 40px 0 0 0;
}

.navbar__dropdown{
  display: none;
}

